import React from 'react';
import { useEditor, EditorContent } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Link from '@tiptap/extension-link';
import styles from './Editor.module.css';

export function Canvas({ content, onChange }) {
  const editor = useEditor({
    extensions: [
      StarterKit,
      Link.configure({
        openOnClick: false,
        validate: href => /^https?:\/\//.test(href),
        HTMLAttributes: {
          class: styles.link,
          rel: 'noopener noreferrer nofollow',
          target: '_blank'
        }
      })
    ],
    content: content || '',
    onUpdate: ({ editor }) => {
      onChange({ target: { value: editor.getHTML() } });
    },
    editorProps: {
      attributes: {
        class: styles.canvas,
        'data-placeholder': "What's on your mind?"
      }
    }
  });

  const setLink = () => {
    const previousUrl = editor?.getAttributes('link')?.href ?? '';
    const url = window.prompt('Enter URL:', previousUrl);
    
    // cancelled
    if (url === null) {
      return;
    }

    // empty
    if (url === '') {
      editor?.chain().focus().unsetLink().run();
      return;
    }

    // add http:// if no protocol is specified
    const validUrl = url.match(/^https?:\/\//) ? url : `https://${url}`;
    editor?.chain().focus().setLink({ href: validUrl }).run();
  };

  const removeLink = () => {
    editor?.chain().focus().unsetLink().run();
  };

  if (!editor) {
    return null;
  }

  return (
    <div>
      <div className={styles.toolbar}>
        <button
          onClick={setLink}
          disabled={!editor.can().setLink()}
          className={styles.toolbarButton}
          title="Add or edit link (âŒ˜K)"
        >
          Add Link
        </button>
        <button
          onClick={removeLink}
          disabled={!editor.isActive('link')}
          className={styles.toolbarButton}
          title="Remove link"
        >
          Remove Link
        </button>
      </div>
      <EditorContent editor={editor} />
    </div>
  );
} 